"use client";

import React, { useState, useEffect } from 'react';
import { motion } from 'framer-motion';
import { useRouter } from 'next/navigation';

import { View, Document, DocumentStatus } from '../../lib/types';
// import * as pdfjs from 'pdfjs-dist'; // Remove direct import

import Spinner from '../../components/Spinner';
import { Upload } from 'lucide-react';
import { useAppContext } from '../../lib/AppContext';
import { Label } from '@/components/ui/label';
import { Input } from '@/components/ui/input';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Button } from '@/components/ui/button';
import { RadioGroup, RadioGroupItem } from '@/components/ui/radio-group';

// Set the worker source for pdfjs-dist (will be set after dynamic import)
// pdfjs.GlobalWorkerOptions.workerSrc = '/pdf.worker.mjs'; // Remove direct setting

const DOCUMENT_TYPES = [
  'Fact Sheet',
  'White Paper',
  'Presentation',
  'Report',
  'Brochure',
  'Other',
];

const PRODUCTION_FREQUENCIES = [
  'Daily',
  'Weekly',
  'Monthly',
  'Quarterly',
  'Annually',
  'Ad-hoc',
];

const TARGET_AUDIENCES = [
  'Retail',
  'Institutional',
  'Advisors',
  'Internal',
  'High Net Worth',
];

const FUND_TYPES = [
  'Open-ended Fund',
  'Close-ended Fund',
  'ETF',
  'Hedge Fund',
  'Private Equity',
  'Real Estate Fund',
];

const UploadPage: React.FC<{}> = () => {

  const { onUploadComplete } = useAppContext();
  const router = useRouter();

  const [projectName, setProjectName] = useState('New Marketing Document');

  const [documentType, setDocumentType] = useState('Fact Sheet');

  const [title, setTitle] = useState('');

  const [file, setFile] = useState<File | null>(null);

  const [isLoading, setIsLoading] = useState(false);

  const [error, setError] = useState<string | null>(null);



  // State for new fields from original spec

  const [firstUseDate, setFirstUseDate] = useState(new Date().toISOString().split('T')[0]);

  const [asOfContentDate, setAsOfContentDate] = useState(new Date().toISOString().split('T')[0]);

  const [productionFrequency, setProductionFrequency] = useState('Monthly');

  const [targetAudience, setTargetAudience] = useState('Retail');

  const [isProductMentioned, setIsProductMentioned] = useState(true);

  const [fundType, setFundType] = useState('Open-ended Fund');

  const [pdfjsLib, setPdfjsLib] = useState<any>(null);

  useEffect(() => {
    // Dynamically import pdfjs-dist on the client-side
    const loadPdfjs = async () => {
      const pdfjs = await import(/* webpackChunkName: "pdfjs-dist", ssr: false */ 'pdfjs-dist');
      pdfjs.GlobalWorkerOptions.workerSrc = '/pdf.worker.mjs';
      setPdfjsLib(pdfjs);
    };
    loadPdfjs();
  }, []);

  const handleSubmit = async (e: React.FormEvent) => {

    e.preventDefault();

    if (!file) {

      setError("Please select a document to upload.");

      return;

    }

    if (!title) {

        setTitle(file.name.replace(/\.[^/.]+$/, ""));

    }

    setIsLoading(true);

    setError(null);



    try {
        if (!pdfjsLib) {
          setError("PDF.js library not loaded. Please try again.");
          setIsLoading(false);
          return;
        }

        const arrayBuffer = await file.arrayBuffer();
        const pdf = await pdfjsLib.getDocument({ data: arrayBuffer }).promise;
        let extractedText = '';
        for (let i = 1; i <= pdf.numPages; i++) {
          const page = await pdf.getPage(i);
          const textContent = await page.getTextContent();
          const pageText = textContent.items.map((item: any) => item.str).join(' ');
          extractedText += `--- PAGE ${i} ---\n${pageText}\n`;
        }

        const newDocId = `doc-${Date.now()}`;

        const newDoc: Document = {

          id: newDocId,

          projectName,

          title: title || file.name.replace(/\.[^/.]+$/, ""),

          type: documentType,

          status: DocumentStatus.InReview,

          lastUpdated: new Date().toISOString().split('T')[0],

        };



        // Simulate a delay for a better UX, then complete with mock data

        setTimeout(() => {

          onUploadComplete(newDoc, extractedText);

          router.push(`/editor?documentId=${encodeURIComponent(newDoc.id)}`);

        }, 1000);

        

    } catch(e) {

        console.error("Failed to process file:", e);

        setError("Could not read or process the file. Please ensure it's a valid PDF and try again.");

        setIsLoading(false);

    }

  };



  if (isLoading) {

      return (

          <div className="flex flex-col items-center justify-center h-screen bg-zinc-50 dark:bg-zinc-950 text-center">

              <Spinner />

              <h2 className="mt-4 text-2xl font-semibold text-gray-800 dark:text-gray-100">Processing Document...</h2>

              <p className="text-gray-600 dark:text-gray-400 mt-2">Finalizing your upload.</p>

          </div>

      );

  }



  return (

    <motion.div

      initial={{ opacity: 0, y: 20 }}

      animate={{ opacity: 1, y: 0 }}

      transition={{ duration: 0.5 }}

      className="min-h-screen bg-zinc-50 dark:bg-zinc-950 p-4 sm:p-6 lg:p-8 flex items-center justify-center"

    >

      <div className="max-w-4xl w-full bg-white dark:bg-zinc-900 p-8 rounded-2xl shadow-xl border border-zinc-200 dark:border-zinc-800">

        <h1 className="text-3xl font-bold text-gray-900 dark:text-gray-50 mb-7 text-center">Upload Document</h1>

        {error && (

          <motion.div

            initial={{ opacity: 0, y: -20 }}

            animate={{ opacity: 1, y: 0 }}

            className="bg-red-100 border border-red-400 text-red-700 px-4 py-3 rounded-lg relative mb-6 dark:bg-red-900 dark:text-red-200"

            role="alert"

          >

            {error}

          </motion.div>

        )}

        <form onSubmit={handleSubmit} className="space-y-6">

          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">

            {/* Project Name */}

            <div>

              <Label htmlFor="projectName" className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1">Project Name</Label>

              <Input

                id="projectName"

                type="text"

                value={projectName}

                onChange={e => setProjectName(e.target.value)}

                required

                className="mt-1 block w-full rounded-md border-zinc-300 dark:border-zinc-700 bg-white dark:bg-zinc-800 text-gray-900 dark:text-gray-50 shadow-sm focus:border-blue-500 focus:ring-blue-500 sm:text-sm transition-colors duration-200 p-2.5"

              />

            </div>

            {/* Document Type */}

            <div>

              <Label htmlFor="documentType" className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1">Document Type</Label>

              <Select

                onValueChange={setDocumentType}

                value={documentType}

              >

                <SelectTrigger id="documentType" className="mt-1 w-full">

                  <SelectValue placeholder="Select a document type" />

                </SelectTrigger>

                <SelectContent>

                  {DOCUMENT_TYPES.map(t => (

                    <SelectItem key={t} value={t}>{t}</SelectItem>

                  ))}

                </SelectContent>

              </Select>

            </div>

            {/* Title of the Piece (Optional) */}

            <div className="md:col-span-2">

              <Label htmlFor="title" className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1">Title of the Piece (Optional)</Label>

              <Input

                id="title"

                type="text"

                value={title}

                onChange={e => setTitle(e.target.value)}

                placeholder="Defaults to filename if left blank"

                className="mt-1 block w-full rounded-md border-zinc-300 dark:border-zinc-700 bg-white dark:bg-zinc-800 text-gray-900 dark:text-gray-50 shadow-sm focus:border-blue-500 focus:ring-blue-500 sm:text-sm transition-colors duration-200 p-2.5"

              />

            </div>

            {/* Anticipated First Use Date */}

            <div>

              <Label htmlFor="firstUseDate" className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1">Anticipated First Use Date</Label>

              <Input

                id="firstUseDate"

                type="date"

                value={firstUseDate}

                onChange={e => setFirstUseDate(e.target.value)}

                required

                className="mt-1 block w-full rounded-md border-zinc-300 dark:border-zinc-700 bg-white dark:bg-zinc-800 text-gray-900 dark:text-gray-50 shadow-sm focus:border-blue-500 focus:ring-blue-500 sm:text-sm transition-colors duration-200 p-2.5"

              />

            </div>

            {/* As of Content Date */}

            <div>

              <Label htmlFor="asOfContentDate" className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1">As of Content Date</Label>

              <Input

                id="asOfContentDate"

                type="date"

                value={asOfContentDate}

                onChange={e => setAsOfContentDate(e.target.value)}

                required

                className="mt-1 block w-full rounded-md border-zinc-300 dark:border-zinc-700 bg-white dark:bg-zinc-800 text-gray-900 dark:text-gray-50 shadow-sm focus:border-blue-500 focus:ring-blue-500 sm:text-sm transition-colors duration-200 p-2.5"

              />

            </div>

            {/* Production Frequency */}

            <div>

              <Label htmlFor="productionFrequency" className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1">Production Frequency</Label>

              <Select

                onValueChange={setProductionFrequency}

                value={productionFrequency}

              >

                <SelectTrigger id="productionFrequency" className="mt-1 w-full">

                  <SelectValue placeholder="Select production frequency" />

                </SelectTrigger>

                <SelectContent>

                  {PRODUCTION_FREQUENCIES.map(f => (

                    <SelectItem key={f} value={f}>{f}</SelectItem>

                  ))}

                </SelectContent>

              </Select>

            </div>

            {/* Target Audience */}

            <div>

              <Label htmlFor="targetAudience" className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1">Target Audience</Label>

              <Select

                onValueChange={setTargetAudience}

                value={targetAudience}

              >

                <SelectTrigger id="targetAudience" className="mt-1 w-full">

                  <SelectValue placeholder="Select target audience" />

                </SelectTrigger>

                <SelectContent>

                  {TARGET_AUDIENCES.map(a => (

                    <SelectItem key={a} value={a}>{a}</SelectItem>

                  ))}

                </SelectContent>

              </Select>

            </div>

            {/* Is a product mentioned? */}

            <div className="md:col-span-2">

              <Label htmlFor="isProductMentioned" className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1">Is a product mentioned?</Label>

              <div className="mt-2 flex items-center space-x-4">

                <RadioGroup

                  defaultValue={isProductMentioned ? "yes" : "no"}

                  onValueChange={(value) => setIsProductMentioned(value === "yes")}

                  className="flex space-x-4"

                >

                  <div className="flex items-center space-x-2">

                    <RadioGroupItem value="yes" id="r1" />

                    <Label htmlFor="r1">Yes</Label>

                  </div>

                  <div className="flex items-center space-x-2">

                    <RadioGroupItem value="no" id="r2" />

                    <Label htmlFor="r2">No</Label>

                  </div>

                </RadioGroup>

              </div>

            </div>

            {/* Fund Type (Conditional) */}

            {isProductMentioned && (

              <div className="md:col-span-2">

                <Label htmlFor="fundType" className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1">Fund Type</Label>

                <Select

                  onValueChange={setFundType}

                  value={fundType}

                >

                  <SelectTrigger id="fundType" className="mt-1 w-full">

                    <SelectValue placeholder="Select fund type" />

                  </SelectTrigger>

                  <SelectContent>

                    {FUND_TYPES.map(f => (

                      <SelectItem key={f} value={f}>{f}</SelectItem>

                    ))}

                  </SelectContent>

                </Select>

              </div>

            )}

            {/* Document File */}

            <div className="md:col-span-2">

              <Label htmlFor="file-upload" className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1">Document File</Label>

              <div

                className="mt-1 flex justify-center px-6 pt-5 pb-6 border-2 border-zinc-300 dark:border-zinc-700 border-dashed rounded-lg cursor-pointer transition-colors duration-200 hover:border-blue-500 dark:hover:border-blue-400"

              >

                <div className="space-y-1 text-center">

                  <Upload className="mx-auto h-12 w-12 text-gray-400 dark:text-gray-500" />

                  <div className="flex text-sm text-gray-600 dark:text-gray-300">

                    <label

                      htmlFor="file-upload"

                      className="relative cursor-pointer rounded-md font-medium text-blue-600 hover:text-blue-500 focus-within:outline-none focus-within:ring-2 focus-within:ring-offset-2 focus-within:ring-blue-500 dark:text-blue-400 dark:hover:text-blue-300"

                    >

                      <span>Upload a file</span>

                      <input

                        id="file-upload"

                        name="file-upload"

                        type="file"

                        className="sr-only"

                        onChange={e => setFile(e.target.files ? e.target.files[0] : null)}

                      />

                    </label>

                    <p className="pl-1">or drag and drop</p>

                  </div>

                  <p className="text-xs text-gray-500 dark:text-gray-400">Any text-based file, up to 10MB</p>

                  {file && <p className="text-sm text-green-600 dark:text-green-400 mt-2">{file.name}</p>}

                </div>

              </div>

            </div>

          </div>

          <div className="flex justify-end space-x-4 pt-6">

            <Button

              type="button"

              onClick={() => router.push('/dashboard')}

              variant="outline"

            >

              Cancel

            </Button>

            <Button

              type="submit"

            >

              Upload File

            </Button>

          </div>

        </form>

      </div>

    </motion.div>

  );

};



export default UploadPage;
